@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;1,200;1,300&display=swap");
:root {
  --main-color:#e8786e;
  --black:#555;
  --light-color:#777;
  --bg:#fceae9;
  --border:.1rem solid rgba(0,0,0,.1);
  --box-shadow:0 .5rem 1.5rem rgba(0,0,0,.1);
}

* {
  font-family: "Nunito", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  transition: all 0.3s ease-out;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  scroll-padding-top: 9rem;
  overflow-x: hidden;
}

section {
  padding: 2rem 9%;
}

.heading {
  text-align: center;
  padding-bottom: 3rem;
  font-size: 4rem;
  color: var(--black);
}
.heading span {
  color: var(--main-color);
  border-radius: 0.5rem;
  background: var(--bg);
  padding: 0 1.5rem;
}

.header {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: #fff;
  box-shadow: var(--box-shadow);
  align-items: center;
  justify-content: space-between;
  padding: 2rem 9%;
}
.header__logo {
  font-size: 2.5rem;
  color: var(--black);
  font-weight: bolder;
}
.header__navbar--item {
  font-size: 1.7rem;
  color: var(--light-color);
  margin-left: 2rem;
}
.header__navbar--item:hover {
  color: var(--main-color);
}

.home {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.5rem;
  padding-top: 10rem;
  background: var(--bg);
  position: relative;
}
.home__content {
  flex: 1 1 45rem;
}
.home__content h3 {
  font-size: 3.5rem;
  color: var(--black);
  line-height: 1.8;
}
.home__content p {
  font-size: 1.7rem;
  color: var(--light-color);
  line-height: 1.8;
  padding: 1rem 0;
}
.home__image {
  flex: 1 1 45rem;
}
.home__image img {
  width: 100%;
}
.home__cloud {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  height: 18rem;
  background-size: 250rem 18rem;
  background: url(../../src/images/cloud.png);
  animation: cloud 30s linear infinite;
}
.home__cloud-1 {
  opacity: 0.5;
  height: 20rem;
  animation-duration: 35s;
}

@keyframes cloud {
  0% {
    background-position-x: 0rem;
  }
  100% {
    background-position-x: -250rem;
  }
}